
export default {
    props: {
        //      
        profile: {
            type: Object,
            required: true
        },
        //      
        getContent: {
            type: Function,
            required: true
        },
        //      
        putContent: {
            type: Function,
            default: null,
            required: false
        },
        //  
        value: String
    },
    methods: {
        //     
        // init() { },
        // applyContent(content) { },

        //  
        registerError(error) {
            this.errors.push(error);
        },
        //  
        registerWarning(warning) {
            this.warnings.push(warning);
        }
    },
    watch: {
        value(value) {
            this.applyContent(value);
        }
    },
    mounted() {
        this.init();
        this.applyContent(this.value);
    },
    data() {
        return {
            errors: [],
            warnings: [],
            data: null
        };
    }
};
